import React, { useEffect, useState } from "react";
import "./styles.css";
import Recipe from "./Recipe";

const App = () => {
  const APP_ID = "a332a839";
  const APP_KEY = "e0ab59d343b387a261196bf24096bf4c";

  const [recipes, setRecipes] = useState([]);

  const [search, setSearch] = useState("");

  const [query, setQuery] = useState("chicken");

  //When app renders the first time, useEffect runs.
  useEffect(() => {
    getRecipes();
    // console.log("Test Effect");
  }, [query]);

  const getRecipes = async () => {
    const response = await fetch(
      `https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`
    );
    const data = await response.json();
    // console.log(data.hits);
    setRecipes(data.hits);
  };

  //event
  const updateSearch = (x) => {
    setSearch(x.target.value);

  };

  const getSearch = (x) => {
    //stops page refresh
    x.preventDefault();
    setQuery(search);
    setSearch("");
  };

  const firstRecipe = recipes[Math.floor(Math.random() * recipes.length)];
  const secondRecipe = recipes[Math.floor(Math.random() * recipes.length)];

  // const firstRecipe = recipes[Math.random(recipes.length)];
    // const firstRecipe = recipes[randomNumber1];
  
  

  return (
    <div className="App">
      <form onSubmit={getSearch} className="search-form">
        <input
          className="search-bar"
          type="text"
          value={search}
          onChange={updateSearch}
        />
        <button className="search-button" type="submit">
          Search
        </button>
      </form>
      <div className="recipes">
        {recipes.map((recipe) => (
          <Recipe
            key={recipe.recipe.label}
            title={recipe.recipe.label}
            calories={recipe.recipe.calories}
            image={recipe.recipe.image}
            ingredients={recipe.recipe.ingredients}
          />
          //title prop
        ))};
        {/* Example for useEffect 
       <h1 onClick ={() => setCounter(counter + 1)}>{counter}</h1> */}
      </div>
    </div>
  );
};
export default App;
